<template>
    <div id="table">
        <iheader :msg="title"></iheader>
        <div class="content">
            <!-- 使用普通el-upload标签+vue获取xlsx数据 -->
            <!-- :on-success="handleSuccess"
                :before-remove="beforeRemove"
                :on-exceed="handleExceed"
                :on-preview="handlePreview"
                :on-remove="handleRemove" 
                :file-list="fileList"
                
                -->
            <el-upload
                class="upload-demo"
                action="https://jsonplaceholder.typicode.com/posts/"
                :on-change="importExcel"
                multiple
                :limit="3"
                accept=".xls,.xlsx"
            >
                <el-button size="small" type="primary"> 点击上传 </el-button>
            </el-upload>

            <el-table :data="tableData">
                <el-table-column prop="data1" label="数据1" width="">
                </el-table-column>
                <el-table-column prop="data2" label="数据2" width="">
                </el-table-column>
                <el-table-column prop="data3" label="数据3" width="">
                </el-table-column>
                <el-table-column prop="data4" label="数据4" width="">
                </el-table-column>
                <el-table-column prop="data5" label="数据5" width="">
                </el-table-column>
                <el-table-column prop="data6" label="数据6" width="">
                </el-table-column>
                <el-table-column prop="data7" label="数据7"> </el-table-column>
            </el-table>

            <el-button @click="show">展示</el-button>
            <pie :echartdata="echartdata" ref="myechart"></pie>
        </div>
    </div>
</template>

<script>
// import XLSX from "xlsx";
import * as XLSX from "xlsx/xlsx.mjs";
import iheader from "@/components/header.vue";
import pie from "@/components/echart/piechart.vue";

export default {
    props: {},
    components: {
        iheader,
        pie,
    },
    data() {
        return {
            title: "table",
            fileList: [],
            tableData: [],
            resdata: [],
            resarr: [0, 0, 0, 0, 0, 0, 0],
            echartdata: [],
        };
    },
    methods: {
        show() {

            this.$refs.myechart.myEcharts();
        },
        // 上传文件的方法:on-change
        importExcel(file) {
            // let file = file.files[0] // 使用传统的input方法需要加上这一步
            const types = file.name.split(".")[1];
            const fileType = [
                "xlsx",
                "xlc",
                "xlm",
                "xls",
                "xlt",
                "xlw",
                "csv",
            ].some((item) => item === types);
            if (!fileType) {
                alert("格式错误！请重新选择");
                return;
            }
            this.file2Xce(file).then((tabJson) => {
                if (tabJson && tabJson.length > 0) {
                    this.xlsxJson = tabJson;
                    // xlsxJson就是解析出来的json数据,数据格式如下
                    // [
                    //   {
                    //     sheetName: sheet1
                    //     sheet: sheetData
                    //   }
                    // ]
                    this.resdata = this.xlsxJson[0].sheet;
                    this.tableData = this.resdata;
                }
            });
            // 计算获取表格数据
            for (let i = 0; i < this.resdata.length; i++) {
                this.resarr[0] += this.resdata[i].data1;
                this.resarr[1] += this.resdata[i].data2;
                this.resarr[2] += this.resdata[i].data3;
                this.resarr[3] += this.resdata[i].data4;
                this.resarr[4] += this.resdata[i].data5;
                this.resarr[5] += this.resdata[i].data6;
                this.resarr[6] += this.resdata[i].data7;
            }
            // 将表格数据转为echart数据
            for (let i = 0; i < this.resarr.length; i++) {
                this.echartdata.push({
                    value: this.resarr[i],
                    name: i,
                });
            }
            this.$refs.myechart.myEcharts();

        },
        file2Xce(file) {
            // eslint-disable-next-line no-unused-vars
            return new Promise(function (resolve, reject) {
                const reader = new FileReader();
                reader.onload = function (e) {
                    const data = e.target.result;
                    this.wb = XLSX.read(data, {
                        type: "binary",
                    });
                    const result = [];
                    this.wb.SheetNames.forEach((sheetName) => {
                        result.push({
                            sheetName: sheetName,
                            sheet: XLSX.utils.sheet_to_json(
                                this.wb.Sheets[sheetName]
                            ),
                        });
                    });
                    resolve(result);
                };
                reader.readAsBinaryString(file.raw);
                // reader.readAsBinaryString(file) // 传统input方法
            });
        },

        // 文件上传成功时的钩子，只获取到数据，未形成表格，load后数据会清空
        // handleSuccess(response, file, fileList) {
        //     let fileObj = file.raw;
        //     // 定义FileReader时,名字不能使用FileReader
        //     const reader = new FileReader();
        //     reader.readAsArrayBuffer(fileObj);
        //     reader.onload = function (event) {
        //         const fileData = event.target.result;
        //         const workbook = XLSX.read(fileData, {
        //             type: "binary",
        //         });
        //         const wsname = workbook.SheetNames[0];

        //         const sheetJson = XLSX.utils.sheet_to_json(
        //             workbook.Sheets[wsname]
        //         );
        //         this.tableData = [];
        //         sheetJson.forEach((item) => {
        //             this.tableData.push({
        //                 data1: item.data1,
        //                 data2: item.data2,
        //                 data3: item.data3,
        //             });
        //         });
        //     };
        // },

        // 文件列表移除文件时的钩子
        // handleRemove(file, fileList) {
        // },
        // 点击文件列表中已上传的文件时的钩子
        // handlePreview(file) {
        // },
        // 文件超出个数限制时的钩子
        // handleExceed(files, fileList) {
        //     this.$message.warning(
        //         `当前限制选择 3 个文件，本次选择了 ${
        //             files.length
        //         } 个文件，共选择了 ${files.length + fileList.length} 个文件`
        //     );
        // },
        // 删除文件之前的钩子，参数为上传的文件和文件列表，若返回 false 或者返回 Promise 且被 reject，则停止删除。
        // beforeRemove(file, fileList) {
        //     return this.$confirm(`确定移除 ${file.name}？`);
        // },
    },
    mounted() {
    },
};
</script>

<style lang="less" scoped>
#table {
    .content {
        width: 90%;
        margin: 50px auto 0;
        border: 1px solid #ccc;

        /deep/ .el-table {
            border: 1px solid #ccc;
            margin-top: 20px;
            margin-bottom: 30px;
            // width: 80%;
            width: 600px;
            .el-table__header {
                .el-table__cell {
                    padding: 5px 0;
                    text-align: center;
                    .cell {
                        color: #999;
                    }
                }
            }
            .el-table__row {
                border: 1px solid pink;
                .el-table__cell {
                    padding: 3px 0;
                    text-align: center;
                    .cell {
                        color: #000;
                    }
                }
            }
        }
    }
}
</style>
